import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ComponentViewBean } from "./../bean/component-view.bean";
import { Message } from 'primeng/primeng';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { ProductManageHttpService } from '../../../http/product-manage.http.service';
@Component({
  selector: 'app-component-view-details',
  templateUrl: './component-view-details.component.html',
  styleUrls: ['./component-view-details.component.scss']
})
export class ComponentViewDetailsComponent implements OnInit, OnChanges {
  tabMesg: any = tableMessage;//列表无数据
  @Input() inValue: any;

  @Output() outValue: EventEmitter<any> = new EventEmitter();
  componentViewBean: ComponentViewBean = new ComponentViewBean();
  cars: any = [];
  //模态框
  showModel: string;
  display: boolean = false;
  //模态框的标题
  headerTitle: string
  updateValue: any = [];
  //提示信息
  msgs: Message[] = [];

  list: any = [];
  productFeatureCategoryId: string;
  listValue: any = [];
  constructor(public productManageHttpService: ProductManageHttpService, ) { }
  //分页
  pageSize: number = 10;
  pageNum: number = 1;
  total: number;
  listValueList: any = [];
  ngOnInit() {
  }

  ngOnChanges() {
    this.queryEvent()
  }
  // 关闭
  closeClick() {
    this.outValue.emit(false);
  }
  queryEvent() {
    this.list = this.inValue;
    let param = {
      productFeatureCategoryId: this.inValue.productFeatureCategoryId,
      pageSize: this.pageSize,
      pageNum: this.pageNum
    };
    this.productManageHttpService.prodFeatureCategoryDetails(param).subscribe(data => {
      if (data.returnCode.code == "success") {
        // this.total = data.total;
        data.resultList.forEach(item => {
          if (item.min == null) {
            if (item.def != null) {
              item.featureValue = 3;
            } else {
              item.featureValue = 1;
            }
          }
          if (item.min != null) {
            item.featureValue = 2;
          }
        });
        // this.listValue = data.resultList;
        this.listValueList = data.resultList;
        if (data.resultList.length > 0) {
          this.total = data.resultList.length;
        } else {
          this.total = 0;
        }
        let currentPages1 = this.pageNum * 10;;
        if (this.listValueList.length > currentPages1) {
          let currentPages2 = currentPages1 - 10;
          this.listValue = this.listValueList.slice(currentPages2, currentPages1);
        } else {
          let currentPages2 = currentPages1 - 10;
          this.listValue = this.listValueList.slice(currentPages2);
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    });
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.pageNum = currentPage;
    //调用查询的方法
    this.queryEvent();
    // this.pageNum = 1;
  }
  //组件详情
  detailsClick(item) {
    item.productFeatureCategoryId = this.inValue.productFeatureCategoryId;
    this.display = true;
    this.headerTitle = '详情';
    this.showModel = '3';
    this.updateValue = item;
  }
  //详情回调
  detailsCall(param) {
    this.display = param;
  }
}
