import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { StepsModule, MenuItem, Message } from 'primeng/primeng';
import { SelectItem } from 'primeng/primeng';
import { ComponentConfigBean } from "./bean/component-config.bean";
import { ConfirmationService } from 'primeng/primeng';//删除
import { ActivatedRoute, Router } from '@angular/router';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ProductManageHttpService } from '../../http/product-manage.http.service';

@Component({
    selector: 'app-component-config',
    templateUrl: './component-config.component.html',
    styleUrls: ['./component-config.component.scss'],
    providers: [ProductManageHttpService, ConfirmationService, Commfunc]
})
/**
 * 组件新增
 */
export class ComponentConfigComponent implements OnInit {
    public tabMesg: any = tableMessage;//列表无数据
    public userform: FormGroup; //校验
    // bean
    public componentConfigBean: ComponentConfigBean = new ComponentConfigBean();
    //修改的参数
    public updateValue: any = [];
    public vlaue1: any;
    public vlaue2: any;//列表型？添值型
    items: MenuItem[];
    public ceshi0: any;
    public ceshi: any;
    public featureType: any;
    code: any;
    productFeatureCategoryIdHead: any = 'PC';
    productFeatureCategoryId: any;
    //模态框
    public showModel: string;
    display: boolean = false;
    //模态框的标题
    public headerTitle: string
    public cars_2: any;//列表
    public cars_2Vluel: any;

    activeIndex: number = 0;
    //提示信息
    public msgs: Message[] = [];
    public accept_value: any = [];
    //分页
    pageSize: number = 10;
    pageNum: number = 1;
    total: number;
    cars_2List: any = [];//列表
    iscompele: boolean = false;
    constructor(public productManageHttpService: ProductManageHttpService,
        public confirmationService: ConfirmationService,
        public router: Router,
        public fb: FormBuilder,
        public commfunc: Commfunc, ) {
        this.userform = fb.group({
            'description': new FormControl('', [Validators.pattern(/^\S+$/), Validators.required]),
            'productFeatureCategoryId': new FormControl('', [Validators.pattern(/^[0-9]\d*$/), Validators.required]),
            parentCategoryId: (''),
        });
        this.codeValues(); // 调用方法，获取全部码值
        this.featureType = this.code['FeatureType'];
    }

    ngOnInit() {
        this.vlaue1 = 0;
        this.items = [{
            label: '新建组件',
            command: (event: any) => {
                this.activeIndex = 0;
            }
        },
        {
            label: '维护属性',
            command: (event: any) => {
                this.activeIndex = 1;
            }
        }];
        this.getSeqNo();
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    // 获取默认编码
    getSeqNo() {
        let parms = {
            seqType: 'PRODUCT_FEATURE_CATEGORY',
        };
        this.productManageHttpService.getSeqNo(parms).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.productFeatureCategoryId = data.seqNo;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        });
    }
    //上一步
    prev() {
        if (this.activeIndex > 0) {
            this.activeIndex = this.activeIndex - 1;
            this.vlaue1 = this.activeIndex;
            this.vlaue2 = this.ceshi;
        }
    }
    //下一步
    next() {
        if (this.activeIndex == 0) {
            // 验证
            for (const i in this.userform.controls) {
                this.userform.controls[i].markAsDirty();
            }
            //处理是否提交
            for (const i in this.userform.controls) {
                if (!this.userform.controls[i].valid) {
                    return;
                }
            }
            this.iscompele = true;
            let productFeatureCategoryId = this.productFeatureCategoryId.split('');
            if (productFeatureCategoryId.length == 8) {
                this.componentConfigBean.productFeatureCategoryId = this.productFeatureCategoryIdHead + this.productFeatureCategoryId;
                this.productManageHttpService.addFeatureCategory(this.componentConfigBean).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        this.accept_value = data;
                        this.activeIndex = this.activeIndex + 1;
                        this.vlaue1 = this.activeIndex;
                        this.vlaue2 = this.ceshi;
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                    } else {
                        this.iscompele = false;
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                    }
                }, error => {
                    this.iscompele = false;
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
                });
            } else {
                this.iscompele = false;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '组件编号为PC+8位数字！' });
            }
        }
        let kk = document.getElementById('KK');
        let contents = kk.getElementsByClassName('ui-steps-number');
        for (let i = 0; i < contents.length; i++) {
            contents[i]['style'].backgroundColor = '#757575';
            if (i <= this.activeIndex) {
                contents[i]['style'].backgroundColor = '#71d071';
            }
            if (i == (this.activeIndex + 1)) {
                contents[i]['style'].backgroundColor = '#ffc107';
            }
        }
    }

    confirm() {
        this.router.navigate(['/pages/tzb/product-manage/module-manage/component-view'])
    }
    //查询
    attr_query() {
        let param = {
            productFeatureCategoryId: this.accept_value.productFeatureCategoryId,
            pageSize: this.pageSize,
            pageNum: this.pageNum
        };
        this.productManageHttpService.prodFeatureCategoryDetails(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.total = data.total;
                data.resultList.forEach(item => {
                    if (item.min == null) {
                        if (item.def != null) {
                            item.featureValue = 3;
                        } else {
                            item.featureValue = 1;
                        }
                    }
                    if (item.min != null) {
                        item.featureValue = 2;
                    }
                });
                // this.cars_2 = data.resultList;
                this.cars_2List = data.resultList;
                if (data.resultList.length > 0) {
                    this.total = data.resultList.length;
                } else {
                    this.total = 0;
                }
                let currentPages1 = this.pageNum * 10;;
                if (this.cars_2List.length > currentPages1) {
                    let currentPages2 = currentPages1 - 10;
                    this.cars_2 = this.cars_2List.slice(currentPages2, currentPages1);
                } else {
                    let currentPages2 = currentPages1 - 10;
                    this.cars_2 = this.cars_2List.slice(currentPages2);
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }
    //新增组件
    addAttribute(item) {
        this.updateValue = this.accept_value.productFeatureCategoryId;
        this.display = true;
        this.headerTitle = '新增';
        this.showModel = '1';
    }
    //新增组件回掉
    addCall(param) {
        if (param.code == 'cancelModel') {
            this.display = param.display;
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: param.code });
            this.display = param.display;
        }
        this.attr_query();
    }
    //修改组件
    modifyAttribute(item) {
        item.productFeatureCategoryId = this.accept_value.productFeatureCategoryId;
        this.display = true;
        this.headerTitle = '修改';
        this.showModel = '2';
        this.updateValue = item;
    }
    //修改组件回掉
    modifyCall(param) {
        if (param.code == 'cancelModel') {
            this.display = param.display;
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: '修改成功!' });
            this.display = param.display;
        }
        this.attr_query();
    }
    //组件详情
    detailsClick(item) {
        item.productFeatureCategoryId = this.accept_value.productFeatureCategoryId;
        this.display = true;
        this.headerTitle = '详情';
        this.showModel = '3';
        this.updateValue = item;
    }
    //详情回掉
    detailsCall(param) {
        this.display = param;
    }
    //删除组件
    deleteClick(item) {
        let param = { productFeatureTypeId: item.productFeatureTypeId, productFeatureCategoryId: this.accept_value.productFeatureCategoryId };
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                this.productManageHttpService.prdcFeaAndFeaTypeDel(param).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        this.attr_query();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
                })
            }
        });
    }
    //分页
    paginate(event) {
        //每页显示的条数
        this.pageSize = event.rows;
        //当前页
        let currentPage = event.page + 1;
        this.pageNum = currentPage;
        //调用查询的方法
        this.attr_query();
        // this.pageNum = 1;
    }
}





