import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//页面

import { ComponentViewComponent } from './component-view/component-view.component';//组件查看
import { ComponentConfigComponent } from './component-config/component-config.component';//组件配置
import { ComponentModifyComponent } from './component-modify/component-modify.component';//组件配置-最终修改
const routes: Routes = [
    {
        path: 'component-view', component: ComponentViewComponent,
    },
    {
        path: 'component-config', component: ComponentConfigComponent,
    },
    {
        path: 'component-modify', component: ComponentModifyComponent,
    },
];

export const ModuleManageRouting: ModuleWithProviders = RouterModule.forChild(routes);
