import { query } from '@angular/animations/src/animation_metadata';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SelectItem } from 'primeng/primeng';
import { ComponentConfigADDBean } from './bean/config-add.bean';
import { Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { checkSpecialCharacter } from '../../../../workflow/activiti-work-flow/common/constant/validator.utils';
import { ProductManageHttpService } from '../../../http/product-manage.http.service';
// import { COMMON_REGEXP} from '../../../../../../../../src/app/pages/tzb/workflow/activiti-work-flow/common/constant/validator.constant';
@Component({
    selector: 'app-component-config-add',
    templateUrl: './component-config-add.component.html',
    styleUrls: ['./component-config-add.component.scss'],
    providers: [Commfunc]
})
/**
 * 属性新增
 */
export class ComponentConfigAddComponent implements OnInit {

    @Input() inValue: any;

    @Output() outValue: EventEmitter<any> = new EventEmitter();

    caddBean: ComponentConfigADDBean = new ComponentConfigADDBean();
    userform: FormGroup; // 校验
    userform02: FormGroup; // 校验属性值类型-数值型
    userform03: FormGroup; // 校验属性值类型-金额型
    userform04: FormGroup; // 校验属性值类型-日期型
    userform05: FormGroup; // 校验属性值类型-文本型
    userform06: FormGroup; // 校验属性值类型-数值后确定型
    vlaue1: any; // 01列表型？02添值型
    attribute_type: any; // 属性类型
    vlaue2: any; // 属性值类型
    value_type: any; // 取值类型
    productFeatureTypeCode: any; // 取值类型
    productFeatureTypeDes: any;
    productFeatureTypeId: any = '';


    items: any = [{
        description: '',
        productFeatureLocalCode: ''
    }];
    // 提示信息
    msgs: Message[] = [];
    // featureType: any[] = FeatureType;
    featureType;
    featureType_list: any = [];
    // valueType: any[] = ValueType;
    valueType;
    valueType_list: any = [];
    valueType_List: any[];

    prdcFeaList: any;
    code: any;

    productFeatureLocalCode = ''; // 添值型本地编码
    description = '';
    min = ''; // 最小值
    def = ''; // 默认值
    inc = ''; // 步长
    max = ''; // 最大值

    itemvlaue12: any = []; // 传值空数组转
    // 日期
    ch: any = CALENDAR_CN; // 讲英文转化为中文
    transferedDateFrom: Date = new Date();
    transferedDateTo: Date = new Date();
    transferedDateDef: Date = new Date();


    constructor(private productManageHttpService: ProductManageHttpService, private fb: FormBuilder, private commfunc: Commfunc, ) {
        this.transferedDateFrom = null;
        this.transferedDateTo = null;
        this.transferedDateDef = null;
        this.userform = fb.group({
            'description': new FormControl('', [Validators.required, Validators.pattern(/^\S+$/)]),
            'attribute_type': new FormControl('', [Validators.required, Validators.pattern(/^\S+$/)]),
            // 'productFeatureTypeId': new FormControl('', [Validators.required, Validators.pattern(/^\S+$/)]),
            'productFeatureTypeCode': new FormControl('', [Validators.required, Validators.pattern(/^[\w\,\|]*$/)]),
            'valueType1': new FormControl('', []),
            'productFeatureTypeDes': new FormControl('', [Validators.pattern(/^\S+$/)]),
            'valueType2': new FormControl('', []),
        });
        this.userform02 = fb.group({
            'inc': new FormControl('', [Validators.pattern(/^[0-9]+(.[0-9]{0,2})?$/)]),
            'def': new FormControl('', [Validators.pattern(/^[0-9]+(.[0-9]{0,2})?$/)]),
            'min': new FormControl('', [Validators.required, Validators.pattern(/^[0-9]+(.[0-9]{0,2})?$/)]),
            'max': new FormControl('', [Validators.required, Validators.pattern(/^[0-9]+(.[0-9]{0,2})?$/)]),
        });
        this.userform03 = fb.group({
            'inc': new FormControl('', [Validators.pattern(/^[0-9]+(.[0-9]{0,2})?$/)]),
            'def': new FormControl('', [Validators.pattern(/^[0-9]+(.[0-9]{0,2})?$/)]),
            'min': new FormControl('', [Validators.required, Validators.pattern(/^[0-9]+(.[0-9]{0,2})?$/)]),
            'max': new FormControl('', [Validators.required, Validators.pattern(/^[0-9]+(.[0-9]{0,2})?$/)]),
        });
        this.userform04 = fb.group({ 
            'transferedDateDef': new FormControl(''),
            'transferedDateFrom': new FormControl('', [Validators.required]),
            'transferedDateTo': new FormControl('', [Validators.required]),
        });
        this.userform05 = fb.group({
            'def': new FormControl('', [Validators.required, Validators.pattern(/^\S+$/)]),
        });
        this.userform06 = fb.group({
            'min': new FormControl('', [Validators.required, Validators.pattern(/^[0-9]+(.[0-9]{0,2})?$/)]),
            'max': new FormControl('', [Validators.required, Validators.pattern(/^[0-9]+(.[0-9]{0,2})?$/)]),
        });
    }

    ngOnInit() {
        this.featureType = [
            { label: '请选择', value: '' },
        ];
        this.valueType = [
            { label: '请选择', value: '' },
        ];
        this.codeValues() //调用方法，获取全部码值
        this.featureType = this.code['FeatureType'];
        this.valueType = this.code['ValueType'];
        this.getSeqNo();
    }
    // 获取默认编码
    getSeqNo() {
        let parms = {
            seqType: 'PRODUCT_FEATURE_TYPE',
        };
        this.productManageHttpService.getSeqNo(parms).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.productFeatureTypeId = 'PCA' + data.seqNo;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        });
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    addBtn() {
        this.items.push({
            description: '',
            productFeatureLocalCode: ''
        });
    }
    detelBtn(i) {
        this.items.splice(i, 1);
    }
    choseVlue1() {
        this.vlaue1 = this.attribute_type;
        if (this.vlaue1 === '01') {
            this.valueType_List = [
                { label: '列表型', value: '01' },
            ];
        }
    }
    choseVlue12() {
        this.vlaue2 = this.value_type;
        if (this.min !== undefined && this.min !== '') {
            this.min = '';
        }
        if (this.max !== undefined && this.max !== '') {
            this.max = '';
        }
        if (this.inc !== undefined && this.inc !== '') {
            this.inc = '';
        }
        if (this.def !== undefined && this.def !== '') {
            this.def = '';
        }
    }
    seveClick() {
        // this.productFeatureTypeId = this.productFeatureTypeId.replace(/(^\s*)|(\s*$)/g, '');
        // let obj = this.productFeatureTypeId.slice(0,3);
        // if (obj != 'PCA') {
        //     this.msgs = [];
        //     this.msgs.push({ severity: 'error', summary: '提示', detail: '属性编号格式错误！' });
        //     return;
        // }
        // if (this.productFeatureTypeId.length != 10) {
        //     this.msgs = [];
        //     this.msgs.push({ severity: 'error', summary: '提示', detail: '属性编号格式错误！' });
        //     return;
        // }
        // if (this.productFeatureTypeId === undefined || this.productFeatureTypeId === '') {
        //     this.msgs = [];
        //     this.msgs.push({ severity: 'error', summary: '提示', detail: '属性编号不能为空！' });
        //     return;
        // }
        // if (this.productFeatureTypeId.replace(/[\u4E00-\u9FA5]/g, '**').length > 20) {
        //     this.msgs = [];
        //     this.msgs.push({ severity: 'error', summary: '提示', detail: '属性编号不可超20字符' });
        //     return;
        // }
        // if (!this.productFeatureTypeId.match(/^[A-Za-z0-9]+$/)) {
        //     this.msgs = [];
        //     this.msgs.push({ severity: 'error', summary: '提示', detail: '属性编号只能由数字和英文字母组成！' });
        //     return;
        // }
        if (this.caddBean.description.replace(/[\u4E00-\u9FA5]/g, '**').length > 100) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '属性名称不可超100字符' });
            return;
        }
        if (this.inc.replace(/[\u4E00-\u9FA5]/g, '**').length > 18) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '步长不能超过18位' });
            return;
        }
        if (this.min.replace(/[\u4E00-\u9FA5]/g, '**').length > 18) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '最小值不能超过18位' });
            return;
        }
        if (this.max.replace(/[\u4E00-\u9FA5]/g, '**').length > 18) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '最大值不能超过18位' });
            return;
        }
        if (this.def !== null) {
            if (this.def !== undefined && this.def !== '') {
                this.def = this.def.replace(/(^\s*)|(\s*$)/g, '');
            }
            if (this.def.replace(/[\u4E00-\u9FA5]/g, '**').length > 18) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '默认值不能超过18位' });
                return;
            }
        }
        // let tempText1 = this.caddBean.description.replace(/\s/g,"");
        // if (tempText1 == "") {
        //     this.msgs = [];
        //     this.msgs.push({ severity: 'error', summary: '提示', detail: '属性名称不能为空' });
        //     return;
        // }
        this.caddBean.description = this.caddBean.description.replace(/(^\s*)|(\s*$)/g, '');
        if (this.caddBean.description === undefined || this.caddBean.description === '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '属性名称不能为空！' });
            return;
        }
        if (this.attribute_type === '02') {
            if (this.value_type === undefined) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '取值类型不可为空' });
                return;
            }
        }
        if (!(this.vlaue1 === '02' && this.vlaue2 === '04')) {
            if (Number(this.max) < Number(this.min)) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '最大值必须大于等于最小值' });
                return;
            }
        }
        // 验证
        // tslint:disable-next-line:forin
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        // 处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        //  判断属性类型    列表型
        let num = 0;
        if (this.vlaue1 === '01') {
            this.itemvlaue12 = [];
            console.log(this.items);
            if (this.items.length > 1) {
                for (let i = 0; i < this.items.length; i++) {
                    for (let j = this.items.length - 1; j > i; j--) {
                        if (this.items[i].productFeatureLocalCode === this.items[j].productFeatureLocalCode) {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '提示', detail: '选项值不可重复！' });
                            return;
                        }
                        if (this.items[i].description === this.items[j].description) {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '提示', detail: '选项值描述不可重复！' });
                            return;
                        }

                    }
                }
            }
            this.items.forEach(item => {
                if (item.productFeatureLocalCode === '' || item.description === '') {
                    num++;
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '选项值或选项值描述不可为空！' });
                    return;
                }
                if (item.productFeatureLocalCode.match(/^[ ]+$/) || item.description.match(/^[ ]+$/)) {
                    num++;
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '选项值或选项值描述不可为空！' });
                    return;
                }
                if (item.productFeatureLocalCode.replace(/[\u4E00-\u9FA5]/g, '**').length > 20) {
                    num++;
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '选项值字段不可超过20字符' });
                    return;
                }
                if (item.description !== '') {
                    // 判断描述字段的位数
                    if (item.description.replace(/[\u4E00-\u9FA5]/g, '**').length > 60) {
                        num++;
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: '选项值描述字段不可超60字符' });
                        return;
                    }
                    item.description = item.description.replace(/(^\s*)|(\s*$)/g, '');
                    item.productFeatureLocalCode = item.productFeatureLocalCode.replace(/(^\s*)|(\s*$)/g, '');
                    this.itemvlaue12.push(item);
                }
            });
            this.caddBean.prdcFeaList = this.itemvlaue12;
            this.caddBean.valueType = '01';
        }
        if (this.vlaue1 === '02') {
            if (this.vlaue2 === '02') {
                const exp = /^(-)?\d+(\.\d+)?$/;
                // tslint:disable-next-line:forin
                for (const i in this.userform02.controls) {
                    this.userform02.controls[i].markAsDirty();
                }
                // 处理是否提交
                for (const i in this.userform02.controls) {
                    if (!this.userform02.controls[i].valid) {
                        return;
                    }
                }
                const tempText_max = this.max.replace(/\s/g, '');
                if (tempText_max === '') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '最大值不能为空' });
                    return;
                }
                if (!exp.test(tempText_max)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '最大值应为数字' });
                    return;
                }
                const tempText_min = this.min.replace(/\s/g, '');
                if (tempText_min === '') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '最小值不能为空' });
                    return;
                }
                if (!exp.test(tempText_min)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '最小值应为数字' });
                    return;
                }
                if (this.inc !== '' && this.inc !== undefined && !exp.test(this.inc)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '步长应为数字' });
                    return;
                }
                if (this.def !== '' && this.def !== undefined && !exp.test(this.def)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '默认值应为数字' });
                    return;
                }
                const incNum = Number(this.inc);
                const minNum = Number(this.min);
                const maxNum = Number(this.max);
                const defNum = Number(this.def);
                if (this.inc !== '' && this.inc !== undefined && incNum > (maxNum - minNum)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '步长不可大于最大值与最小值的差值' });
                    return;
                }
                if (this.def !== '' && this.def !== undefined && minNum > defNum) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '默认值不可小于最小值' });
                    return;
                }
                if (this.def !== '' && this.def !== undefined && defNum > maxNum) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '默认值不可大于最大值' });
                    return;
                }
                this.prdcFeaList = {
                    description: this.description,
                    min: this.min,
                    def: this.def,
                    inc: this.inc,
                    max: this.max,
                };
                this.itemvlaue12 = [];
                this.itemvlaue12.push(this.prdcFeaList);
                this.caddBean.prdcFeaList = this.itemvlaue12;
            }
            if (this.vlaue2 === '03') {
                // tslint:disable-next-line:forin
                for (const i in this.userform03.controls) {
                    this.userform03.controls[i].markAsDirty();
                }
                // 处理是否提交
                for (const i in this.userform03.controls) {
                    if (!this.userform03.controls[i].valid) {
                        return;
                    }
                }
                const exp = /^[+]{0,1}(\d+)$|^[+]{0,1}(\d+\.\d+)$/;
                const tempText_max = this.max.replace(/\s/g, '');
                if (tempText_max === '') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '最大值不能为空' });
                    return;
                }
                if (tempText_max === '0') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '最大值不能为0' });
                    return;
                }
                const tempText_min = this.min.replace(/\s/g, '');
                if (tempText_min === '') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '最小值不能为空' });
                    return;
                }
                if (!exp.test(tempText_max)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '最大值应为非负数字' });
                    return;
                }
                if (this.def !== '' && this.def !== undefined && !exp.test(this.def)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '默认值应为非负数字' });
                    return;
                }
                if (!exp.test(tempText_min)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '最小值应为非负数字' });
                    return;
                }
                const exp1 = /^(-)?\d+(\.\d+)?$/;
                if (this.inc !== '' && this.inc !== undefined && !exp1.test(this.inc)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '步长应为数字' });
                    return;
                }
                const exp2 = /^[0-9]{0,15}(\.[0-9]{0,2})?$/;
                const minLength = this.min.indexOf('.');
                const maxLength = this.max.indexOf('.');
                const defLength = this.def.indexOf('.');
                if (tempText_min !== '0') {
                    if (minLength > 15 || (minLength < 0 && this.min.length > 15) || !exp2.test(this.min)) {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: '最小值整数长度不能超过15位，小数位不能超过2位!' });
                        return;
                    }
                }
                if (maxLength > 15 || (maxLength < 0 && this.max.length > 15) || !exp2.test(this.max)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '最大值整数长度不能超过15位，小数位不能超过2位!' });
                    return;
                }
                const incNum = Number(this.inc);
                const minNum = Number(this.min);
                const maxNum = Number(this.max);
                if (this.inc !== '' && this.inc !== undefined && incNum > (maxNum - minNum)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '步长不可大于最大值与最小值的差值' });
                    return;
                }
                if (this.def !== '' && this.def !== undefined) {
                    const tempText_def = this.def.replace(/\s/g, '');
                    if (tempText_def !== '0') {
                        if (defLength > 15 || (defLength < 0 && this.def.length > 15) || !exp2.test(this.def)) {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '提示', detail: '默认值整数长度不能超过15位，小数位不能超过2位!' });
                            return;
                        }
                    }
                    const minNum = Number(this.min);
                    const maxNum = Number(this.max);
                    const defNum = Number(this.def);
                    if (minNum > defNum) {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: '默认值不可小于最小值' });
                        return;
                    }
                    if (defNum > maxNum) {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: '默认值不可大于最大值' });
                        return;
                    }
                }
                this.prdcFeaList = {
                    description: this.description,
                    inc: this.inc,
                    min: this.min,
                    def: this.def,
                    max: this.max,
                };
                this.itemvlaue12 = [];
                this.itemvlaue12.push(this.prdcFeaList);
                this.caddBean.prdcFeaList = this.itemvlaue12;
            }
            if (this.vlaue2 === '04') {
                // // tslint:disable-next-line:forin
                // for (const i in this.userform04.controls) {
                //     this.userform04.controls[i].markAsDirty();
                // }
                // // 处理是否提交
                // for (const i in this.userform04.controls) {
                //     if (!this.userform04.controls[i].valid) {
                //         return;
                //     }
                // }
                this.min = this.transDateNW(this.transferedDateFrom);
                this.max = this.transDateNW(this.transferedDateTo);
                this.def = this.transDateNW(this.transferedDateDef);
                if (this.min === '' || this.min === undefined) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '请填写最小值！' });
                    return;
                }
                if (this.max === '' || this.max === undefined) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '请填写最大值！' });
                    return;
                }
                if (this.def !== '' && this.def !== undefined) {
                    const minDate = Number(this.min);
                    const maxDate = Number(this.max);
                    const defDate = Number(this.def);
                    if (minDate > defDate) {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: '默认值不可小于最小值' });
                        return;
                    } else if (defDate > maxDate) {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: '默认值不可大于最大值' });
                        return;
                    }
                } else {
                    this.def = null;
                }
                this.prdcFeaList = {
                    description: this.description,
                    min: this.min,
                    def: this.def,
                    max: this.max,
                };
                this.itemvlaue12 = [];
                this.itemvlaue12.push(this.prdcFeaList);
                this.caddBean.prdcFeaList = this.itemvlaue12;
            }
            if (this.vlaue2 === '05') {
                for (const i in this.userform05.controls) {
                    this.userform05.controls[i].markAsDirty();
                }
                // 处理是否提交
                for (const i in this.userform05.controls) {
                    if (!this.userform05.controls[i].valid) {
                        return;
                    }
                }
                this.prdcFeaList = {
                    description: this.description,
                    def: this.def,
                };
                this.itemvlaue12 = [];
                this.itemvlaue12.push(this.prdcFeaList);
                this.caddBean.prdcFeaList = this.itemvlaue12;
            }
            if (this.vlaue2 === '06') {
                // tslint:disable-next-line:forin
                for (const i in this.userform06.controls) {
                    this.userform06.controls[i].markAsDirty();
                }
                // 处理是否提交
                for (const i in this.userform06.controls) {
                    if (!this.userform06.controls[i].valid) {
                        return;
                    }
                }
                this.prdcFeaList = {
                    description: this.description,
                    min: this.min,
                    max: this.max,
                };
                this.itemvlaue12 = [];
                this.itemvlaue12.push(this.prdcFeaList);
                this.caddBean.prdcFeaList = this.itemvlaue12;
            }
            this.caddBean.valueType = this.value_type;
        }
        this.caddBean.productFeatureCategoryId = this.inValue;
        this.caddBean.featureType = this.attribute_type;
        this.caddBean.productFeatureTypeId = this.productFeatureTypeId;
        this.caddBean.productFeatureTypeLocal = this.productFeatureTypeId;
        this.caddBean.productFeatureTypeCode = this.productFeatureTypeCode;
        this.caddBean.productFeatureTypeDes = this.productFeatureTypeDes;
        // 提交服务
        if (num === 0) {
            this.productManageHttpService.prdcFeaAndFeaTypeAdd(this.caddBean).subscribe(data => {
                if (data.returnCode.code === 'success') {
                    this.caddBean = new ComponentConfigADDBean();
                    this.productFeatureTypeId = '';
                    this.items = [{
                        description: '',
                        productFeatureLocalCode: ''
                    }];
                    this.description = '';
                    this.min = '';
                    this.max = '';
                    this.def = '';
                    this.inc = '';
                    this.transferedDateFrom = null;
                    this.transferedDateTo = null;
                    this.transferedDateDef = null;
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: '新增成功' });
                    this.goBack();
                } else if (data.returnCode.code === '77777777') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
            });
        }
    }
    goBack() {
        const parm = {
            display: false,
            code: 'cancelModel'
        };
        this.outValue.emit(parm);
    }
    transDateNW(value) {
        if (!value) {
            return '';
        }
        const time = new Date(value);
        const year = value.getFullYear();
        let month = value.getMonth() + 1 + '';
        let data = value.getDate() + '';
        if (month.length < 2) {
            month = '0' + month;
        }
        if (data.length < 2) {
            data = '0' + data;
        }
        const newtime = year + month + data;
        return newtime;
    }
}

