import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { StepsModule, MenuItem, Message } from 'primeng/primeng';
import { SelectItem } from 'primeng/primeng';
import { ProductManageHttpService } from '../../http/product-manage.http.service';
import { ComponentConfigBean } from './../component-config/bean/component-config.bean';
import { ConfirmationService } from 'primeng/primeng'; // 删除
import { ActivatedRoute, Router } from '@angular/router';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { tableMessage } from "app/@uisftech/common/constant/config";

@Component({
    selector: 'app-component-modify',
    templateUrl: './component-modify.component.html',
    styleUrls: ['./component-modify.component.scss'],
    providers: [ProductManageHttpService, ConfirmationService]
})
export class ComponentModifyComponent implements OnInit {
    // bean
    componentConfigBean: ComponentConfigBean = new ComponentConfigBean();
    // 修改的参数
    updateValue: any = [];
    select_1: SelectItem[] = []; // 默认下拉
    select_2: SelectItem[] = []; // 默认下拉
    vlaue1: any;
    vlaue2: any; // 列表型？添值型
    items: MenuItem[];
    // 头部标题
    all_title = '组件修改';
    ceshi0: any;
    ceshi: any;
    // 模态框
    showModel: string;
    display = false;
    // 模态框的标题
    headerTitle: string;
    cars_2: any; // 列表

    cars_2_IsNotEmpty = false;
    cars_2Vluel: any; // 列表判断添置型还是列表型
    userform: FormGroup; // 校验
    activeIndex = 0;
    //分页
    pageSize: number = 10;
    pageNum: number = 1;
    total: number;
    // 提示信息
    msgs: Message[] = [];
    accept_value: any = [];
    // 组件ID
    productFeatureCategoryId: any; // 组件ID
    tabMesg: any = tableMessage;//列表无数据
    cars_2List: any = [];//列表
    iscompele: boolean = false;
    constructor(private productManageHttpService: ProductManageHttpService,
        private confirmationService: ConfirmationService,
        private router: Router,
        private fb: FormBuilder,
        private routers: ActivatedRoute) {
        this.userform = fb.group({
            formControlName: [''],
            'description': new FormControl('', [Validators.pattern(/^\S+$/), Validators.required]),
            'parentCategoryId': new FormControl('', []),
            'productFeatureCategoryId': new FormControl('', []),
        });
    }

    ngOnInit() {
        this.routers.params.subscribe((data) => {
            this.productFeatureCategoryId = data.productFeatureCategoryId;
            if (data.description === undefined) {
                this.componentConfigBean.description = '';
            } else {
                this.componentConfigBean.description = data.description;
            }
            if (data.parentCategoryId === undefined) {
                this.componentConfigBean.parentCategoryId = '';
            } else {
                this.componentConfigBean.parentCategoryId = data.parentCategoryId;
            }

            this.accept_value.productFeatureCategoryId = data.productFeatureCategoryId;
        });
        this.queryEvent();
        this.vlaue1 = 0;
        this.select_2 = [
            { label: '请选择', value: ' ' },
        ];
        this.items = [{
            label: '修改组件',
            command: (event: any) => {
                this.activeIndex = 0;
            }
        },
        {
            label: '维护属性',
            command: (event: any) => {
                this.activeIndex = 1;
            }
        },];
    }

    queryEvent() {
        const param = { productFeatureCategoryId: this.productFeatureCategoryId };
        this.productManageHttpService.prodFeatureCategoryDetails(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                data.resultList.forEach(item => {
                    if (JSON.stringify(item) === '{}') {
                        this.cars_2_IsNotEmpty = false;
                    } else {
                        this.cars_2_IsNotEmpty = true;
                    }
                    if (item.min == null) {
                        item.featureValue = 1;
                    }
                    if (item.min != null) {
                        item.featureValue = 2;
                    }
                });
                this.cars_2 = data.resultList;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
        });
    }
    prev() {
        if (this.activeIndex > 0) {
            this.activeIndex = this.activeIndex - 1;
            this.vlaue1 = this.activeIndex;
            this.vlaue2 = this.ceshi;
        }
    }
    next() {
        if (this.activeIndex === 0) {
            const Dscliste = this.componentConfigBean.description.split('');
            if (Dscliste.length > 20) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '组件名称不能超过20位' });
                return;
            }
            this.componentConfigBean.description = this.componentConfigBean.description.replace(/(^\s*)|(\s*$)/g, '');
            if (this.componentConfigBean.description === undefined || this.componentConfigBean.description === '') {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '组件名称不能为空！' });
                return;
            }
            if (undefined !== this.componentConfigBean.parentCategoryId) {
                this.componentConfigBean.parentCategoryId = this.componentConfigBean.parentCategoryId.replace(/(^\s*)|(\s*$)/g, '');
                if (this.componentConfigBean.parentCategoryId.replace(/[\u4E00-\u9FA5]/g, '**').length > 500) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '描述字段不能超过500位' });
                    return;
                }
            }
            this.iscompele = true;
            //   修改组件回显的内容
            const parm = {
                productFeatureCategoryId: this.productFeatureCategoryId,
                parentCategoryId: this.componentConfigBean.parentCategoryId,
                description: this.componentConfigBean.description,
            };

            // 修改组件
            // const redo_name = {
            //     categoryId: this.productFeatureCategoryId,
            //     categoryName: this.componentConfigBean.description,
            //     productCategoryTypeId: 'zujian'
            // };
            // this.productManageHttpService.queryIdAndNameIsNotExist(redo_name).subscribe(data => {
            //     if (data.returnCode.code == "success") {
            //         if (data.isOrNot === 'not') {
            // 提交服务 
            // tslint:disable-next-line:no-shadowed-variable
            this.productManageHttpService.updateFeatureCategory(parm).subscribe(data => {
                if (data.returnCode.code == "success") {
                    // this.accept_value = data;
                    this.accept_value = parm;
                    this.activeIndex = this.activeIndex + 1;
                    this.vlaue1 = this.activeIndex;
                    this.vlaue2 = this.ceshi;
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: '修改成功' });
                    this.attr_query();
                } else {
                    this.iscompele = false;
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage }); // data.Header.RetMsg
                }
            }, error => {
                this.iscompele = false;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
            });
            //         } else {
            //             this.msgs = [];
            //             this.msgs.push({ severity: 'error', summary: '提示', detail: '修改组件名称重复' });
            //         }
            //     } else {
            //         this.msgs = [];
            //         this.msgs.push({ severity: 'error', summary: '提示', detail: data.Header.RetMsg });
            //     }
            // }, error => {
            //     this.msgs = [];
            //     this.msgs.push({ severity: 'error', summary: '提示', detail: '调用验证重名服务错误' });
            // });

        }
        let kk = document.getElementById('KK');
        let contents = kk.getElementsByClassName('ui-steps-number');
        for (let i = 0; i < contents.length; i++) {
            contents[i]['style'].backgroundColor = '#757575';
            if (i <= this.activeIndex) {
                contents[i]['style'].backgroundColor = '#71d071';
            }
            if (i == (this.activeIndex + 1)) {
                contents[i]['style'].backgroundColor = '#ffc107';
            }
        }
    }

    // 返回
    goBack() {
        window.history.go(-1);
    }
    //  取消
    cancle() {
        this.router.navigate(['/pages/tzb/product-manage/module-manage/component-view']);
    }
    //组件详情
    detailsClick(item) {
        item.productFeatureCategoryId = this.accept_value.productFeatureCategoryId;
        this.display = true;
        this.headerTitle = '详情';
        this.showModel = '3';
        this.updateValue = item;
    }
    //详情回掉
    detailsCall(param) {
        this.display = param;
    }
    // 完成
    confirm() {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '组件配置修改完成' });
        const this_ = this;
        setTimeout(function () {
            this_.router.navigate(['/pages/tzb/product-manage/module-manage/component-view']);
        }, 500);
    }
    // 查询
    attr_query() {
        const parm = {
            productFeatureCategoryId: this.accept_value.productFeatureCategoryId,
            pageSize: this.pageSize,
            pageNum: this.pageNum
        };
        this.productManageHttpService.prodFeatureCategoryDetails(parm).subscribe(data => {
            if (data.returnCode.code == "success") {
                data.resultList.forEach(item => {
                    if (item.min == null) {
                        if (item.def != null) {
                            item.featureValue = 3;
                        } else {
                            item.featureValue = 1;
                        }
                    }
                    if (item.min != null) {
                        item.featureValue = 2;
                    }
                });
                // this.cars_2 = data.resultList;
                this.cars_2List = data.resultList;
                if (data.resultList.length > 0) {
                    this.total = data.resultList.length;
                } else {
                    this.total = 0;
                }
                let currentPages1 = this.pageNum * 10;;
                if (this.cars_2List.length > currentPages1) {
                    let currentPages2 = currentPages1 - 10;
                    this.cars_2 = this.cars_2List.slice(currentPages2, currentPages1);
                } else {
                    let currentPages2 = currentPages1 - 10;
                    this.cars_2 = this.cars_2List.slice(currentPages2);
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
        });
    }
    // 新增组件
    addAttribute(item) {
        this.updateValue = this.accept_value.productFeatureCategoryId;
        this.display = true;
        this.headerTitle = '新增';
        this.showModel = '1';
    }
    // 新增组件回掉
    addCall(param) {
        if (param.code === 'cancelModel') {
            this.display = param.display;
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: '新增属性成功' });
            this.display = param.display;
        }
        this.attr_query();
    }
    // 修改组件
    modifyAttribute(item) {
        this.display = true;
        this.headerTitle = '修改';
        this.showModel = '2';
        this.updateValue = {
            productFeatureTypeId: item.productFeatureTypeId,
            productFeatureCategoryId: this.accept_value.productFeatureCategoryId,
        };
    }
    // 修改组件回掉
    modifyCall(param) {
        if (param.code === 'cancelModel') {
            this.display = param.display;
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: '修改属性成功' });
            this.display = param.display;
        }
        this.attr_query();
    }
    // 删除组件
    deleteClick(item) {
        const param = {
            productFeatureTypeId: item.productFeatureTypeId,
            productFeatureCategoryId: this.accept_value.productFeatureCategoryId
        };
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                this.productManageHttpService.prdcFeaAndFeaTypeDel(param).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: '删除成功' });
                        this.attr_query();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage }); // data.Header.RetMsg
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '删除失败' });
                });
            }
        });
    }
    //分页
    paginate(event) {
        //每页显示的条数
        this.pageSize = event.rows;
        //当前页
        let currentPage = event.page + 1;
        this.pageNum = currentPage;
        //调用查询的方法
        this.attr_query();
        // this.pageNum = 1;
    }
}





